"use client";

import React, { useEffect, useState } from "react";
import { useAuth } from "@/hooks/useAuth";

interface AttendanceRecord {
  id: number;
  date: string;
  subject: string;
  status: "PRESENT" | "ABSENT" | "LATE";
  markedBy: string;
}

interface AttendanceStats {
  totalClasses: number;
  presentClasses: number;
  absentClasses: number;
  lateClasses: number;
  attendancePercentage: number;
}

const AttendancePage: React.FC = () => {
  const { user } = useAuth();
  const [attendanceRecords, setAttendanceRecords] = useState<AttendanceRecord[]>([]);
  const [stats, setStats] = useState<AttendanceStats>({
    totalClasses: 0,
    presentClasses: 0,
    absentClasses: 0,
    lateClasses: 0,
    attendancePercentage: 0
  });
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [selectedMonth, setSelectedMonth] = useState(new Date().getMonth());
  const [selectedYear, setSelectedYear] = useState(new Date().getFullYear());

  useEffect(() => {
    const fetchAttendance = async () => {
      try {
        // Sample attendance data
        const sampleRecords: AttendanceRecord[] = [
          { id: 1, date: "2024-01-15", subject: "Mathematics", status: "PRESENT", markedBy: "Dr. Smith" },
          { id: 2, date: "2024-01-15", subject: "Physics", status: "PRESENT", markedBy: "Prof. Johnson" },
          { id: 3, date: "2024-01-16", subject: "Chemistry", status: "LATE", markedBy: "Dr. Wilson" },
          { id: 4, date: "2024-01-16", subject: "English", status: "PRESENT", markedBy: "Ms. Brown" },
          { id: 5, date: "2024-01-17", subject: "Mathematics", status: "ABSENT", markedBy: "Dr. Smith" },
          { id: 6, date: "2024-01-17", subject: "Computer Science", status: "PRESENT", markedBy: "Prof. Davis" },
          { id: 7, date: "2024-01-18", subject: "Physics", status: "PRESENT", markedBy: "Prof. Johnson" },
          { id: 8, date: "2024-01-18", subject: "Chemistry", status: "PRESENT", markedBy: "Dr. Wilson" },
          { id: 9, date: "2024-01-19", subject: "English", status: "LATE", markedBy: "Ms. Brown" },
          { id: 10, date: "2024-01-19", subject: "Mathematics", status: "PRESENT", markedBy: "Dr. Smith" },
          { id: 11, date: "2024-01-22", subject: "Computer Science", status: "PRESENT", markedBy: "Prof. Davis" },
          { id: 12, date: "2024-01-22", subject: "Physics", status: "ABSENT", markedBy: "Prof. Johnson" },
          { id: 13, date: "2024-01-23", subject: "Chemistry", status: "PRESENT", markedBy: "Dr. Wilson" },
          { id: 14, date: "2024-01-23", subject: "English", status: "PRESENT", markedBy: "Ms. Brown" },
          { id: 15, date: "2024-01-24", subject: "Mathematics", status: "PRESENT", markedBy: "Dr. Smith" }
        ];

        setAttendanceRecords(sampleRecords);

        // Calculate stats
        const totalClasses = sampleRecords.length;
        const presentClasses = sampleRecords.filter(r => r.status === "PRESENT").length;
        const absentClasses = sampleRecords.filter(r => r.status === "ABSENT").length;
        const lateClasses = sampleRecords.filter(r => r.status === "LATE").length;
        const attendancePercentage = totalClasses > 0 ? Math.round((presentClasses / totalClasses) * 100) : 0;

        setStats({
          totalClasses,
          presentClasses,
          absentClasses,
          lateClasses,
          attendancePercentage
        });

      } catch (err) {
        setError(err instanceof Error ? err.message : "An error occurred");
      } finally {
        setLoading(false);
      }
    };

    fetchAttendance();
  }, [user, selectedMonth, selectedYear]);

  const getStatusColor = (status: string) => {
    switch (status) {
      case "PRESENT": return "text-green-700 bg-green-100 border-green-200";
      case "ABSENT": return "text-red-700 bg-red-100 border-red-200";
      case "LATE": return "text-yellow-700 bg-yellow-100 border-yellow-200";
      default: return "text-gray-700 bg-gray-100 border-gray-200";
    }
  };

  const getStatusIcon = (status: string) => {
    switch (status) {
      case "PRESENT": return "‚úÖ";
      case "ABSENT": return "‚ùå";
      case "LATE": return "‚è∞";
      default: return "‚ùì";
    }
  };

  const getAttendanceColor = (percentage: number) => {
    if (percentage >= 90) return "text-green-600";
    if (percentage >= 75) return "text-yellow-600";
    return "text-red-600";
  };

  const months = [
    "January", "February", "March", "April", "May", "June",
    "July", "August", "September", "October", "November", "December"
  ];

  if (loading) {
    return (
      <div className="p-8">
        <div className="animate-pulse">
          <div className="h-8 bg-gray-200 rounded w-1/4 mb-8"></div>
          <div className="grid grid-cols-1 md:grid-cols-4 gap-6 mb-8">
            {[...Array(4)].map((_, i) => (
              <div key={i} className="h-32 bg-gray-200 rounded-xl"></div>
            ))}
          </div>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="p-8">
        <div className="bg-red-50 border border-red-200 rounded-xl p-6">
          <div className="flex items-center">
            <span className="text-2xl mr-3">‚ùå</span>
            <div>
              <h3 className="text-lg font-semibold text-red-800">Error Loading Attendance</h3>
              <p className="text-red-600">{error}</p>
            </div>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="p-8 space-y-8">
      {/* Header */}
      <div className="bg-gradient-to-r from-teal-600 to-cyan-600 rounded-2xl p-8 text-white">
        <h1 className="text-3xl font-bold mb-2 flex items-center">
          <span className="text-4xl mr-4">üìã</span>
          Attendance Tracker
        </h1>
        <p className="text-teal-100 text-lg">
          Monitor your class attendance and academic progress
        </p>
      </div>

      {/* Filters */}
      <div className="bg-white rounded-xl shadow-lg p-6">
        <div className="flex flex-wrap gap-4 items-center">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">Month</label>
            <select
              value={selectedMonth}
              onChange={(e) => setSelectedMonth(parseInt(e.target.value))}
              className="border border-gray-300 rounded-lg px-3 py-2 focus:ring-2 focus:ring-teal-500 focus:border-transparent"
            >
              {months.map((month, index) => (
                <option key={index} value={index}>{month}</option>
              ))}
            </select>
          </div>
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">Year</label>
            <select
              value={selectedYear}
              onChange={(e) => setSelectedYear(parseInt(e.target.value))}
              className="border border-gray-300 rounded-lg px-3 py-2 focus:ring-2 focus:ring-teal-500 focus:border-transparent"
            >
              <option value={2024}>2024</option>
              <option value={2023}>2023</option>
            </select>
          </div>
        </div>
      </div>

      {/* Stats Cards */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
        <div className="bg-gradient-to-br from-blue-500 to-blue-600 rounded-xl p-6 text-white">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-blue-100 text-sm font-medium">Total Classes</p>
              <p className="text-3xl font-bold mt-2">{stats.totalClasses}</p>
            </div>
            <div className="text-4xl opacity-80">üìö</div>
          </div>
        </div>
        
        <div className="bg-gradient-to-br from-green-500 to-green-600 rounded-xl p-6 text-white">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-green-100 text-sm font-medium">Present</p>
              <p className="text-3xl font-bold mt-2">{stats.presentClasses}</p>
            </div>
            <div className="text-4xl opacity-80">‚úÖ</div>
          </div>
        </div>
        
        <div className="bg-gradient-to-br from-red-500 to-red-600 rounded-xl p-6 text-white">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-red-100 text-sm font-medium">Absent</p>
              <p className="text-3xl font-bold mt-2">{stats.absentClasses}</p>
            </div>
            <div className="text-4xl opacity-80">‚ùå</div>
          </div>
        </div>
        
        <div className="bg-gradient-to-br from-yellow-500 to-yellow-600 rounded-xl p-6 text-white">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-yellow-100 text-sm font-medium">Late</p>
              <p className="text-3xl font-bold mt-2">{stats.lateClasses}</p>
            </div>
            <div className="text-4xl opacity-80">‚è∞</div>
          </div>
        </div>
      </div>

      {/* Attendance Percentage */}
      <div className="bg-white rounded-xl shadow-lg p-6">
        <h2 className="text-xl font-bold text-gray-900 mb-6 flex items-center">
          <span className="text-2xl mr-3">üìä</span>
          Attendance Overview
        </h2>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
          <div>
            <div className="flex items-center justify-between mb-4">
              <span className="text-lg font-semibold text-gray-700">Overall Attendance</span>
              <span className={`text-3xl font-bold ${getAttendanceColor(stats.attendancePercentage)}`}>
                {stats.attendancePercentage}%
              </span>
            </div>
            <div className="w-full bg-gray-200 rounded-full h-4">
              <div 
                className={`h-4 rounded-full transition-all duration-500 ${
                  stats.attendancePercentage >= 90 ? 'bg-gradient-to-r from-green-500 to-green-600' :
                  stats.attendancePercentage >= 75 ? 'bg-gradient-to-r from-yellow-500 to-yellow-600' :
                  'bg-gradient-to-r from-red-500 to-red-600'
                }`}
                style={{ width: `${stats.attendancePercentage}%` }}
              ></div>
            </div>
            <div className="mt-4 text-sm text-gray-600">
              {stats.attendancePercentage >= 90 && "Excellent attendance! Keep it up! üéâ"}
              {stats.attendancePercentage >= 75 && stats.attendancePercentage < 90 && "Good attendance, but there's room for improvement. üëç"}
              {stats.attendancePercentage < 75 && "Attendance is below recommended level. Please improve. ‚ö†Ô∏è"}
            </div>
          </div>
          <div>
            <h3 className="text-lg font-semibold text-gray-700 mb-4">Breakdown</h3>
            <div className="space-y-3">
              <div className="flex justify-between items-center">
                <span className="text-green-600 flex items-center">
                  <span className="mr-2">‚úÖ</span> Present
                </span>
                <span className="font-semibold">{stats.presentClasses} classes</span>
              </div>
              <div className="flex justify-between items-center">
                <span className="text-red-600 flex items-center">
                  <span className="mr-2">‚ùå</span> Absent
                </span>
                <span className="font-semibold">{stats.absentClasses} classes</span>
              </div>
              <div className="flex justify-between items-center">
                <span className="text-yellow-600 flex items-center">
                  <span className="mr-2">‚è∞</span> Late
                </span>
                <span className="font-semibold">{stats.lateClasses} classes</span>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Attendance Records */}
      <div className="bg-white rounded-xl shadow-lg overflow-hidden">
        <div className="bg-gradient-to-r from-teal-500 to-cyan-500 px-6 py-4">
          <h2 className="text-xl font-bold text-white flex items-center">
            <span className="text-2xl mr-3">üìã</span>
            Attendance Records
          </h2>
        </div>
        
        <div className="overflow-x-auto">
          <table className="w-full">
            <thead className="bg-gray-50">
              <tr>
                <th className="px-6 py-4 text-left text-sm font-semibold text-gray-900">Date</th>
                <th className="px-6 py-4 text-left text-sm font-semibold text-gray-900">Subject</th>
                <th className="px-6 py-4 text-left text-sm font-semibold text-gray-900">Status</th>
                <th className="px-6 py-4 text-left text-sm font-semibold text-gray-900">Marked By</th>
              </tr>
            </thead>
            <tbody className="divide-y divide-gray-200">
              {attendanceRecords.map((record, index) => (
                <tr key={record.id} className={`hover:bg-gray-50 transition-colors ${index % 2 === 0 ? 'bg-white' : 'bg-gray-50/50'}`}>
                  <td className="px-6 py-4">
                    <div className="font-medium text-gray-900">
                      {new Date(record.date).toLocaleDateString('en-US', { 
                        weekday: 'short', 
                        year: 'numeric', 
                        month: 'short', 
                        day: 'numeric' 
                      })}
                    </div>
                  </td>
                  <td className="px-6 py-4">
                    <div className="font-semibold text-gray-900">{record.subject}</div>
                  </td>
                  <td className="px-6 py-4">
                    <span className={`inline-flex items-center px-3 py-1 rounded-full text-sm font-medium border ${getStatusColor(record.status)}`}>
                      <span className="mr-2">{getStatusIcon(record.status)}</span>
                      {record.status}
                    </span>
                  </td>
                  <td className="px-6 py-4 text-gray-600">{record.markedBy}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
        
        {attendanceRecords.length === 0 && (
          <div className="text-center py-12">
            <div className="text-6xl mb-4">üìã</div>
            <h3 className="text-lg font-semibold text-gray-900 mb-2">No Attendance Records</h3>
            <p className="text-gray-600">Attendance records will appear here once classes begin.</p>
          </div>
        )}
      </div>
    </div>
  );
};

export default AttendancePage;
